import React, { Component } from 'react'
import { Carousel} from 'antd'
import Shengjunjiangzhu from './component/Shengjunjiangzhu'
import Xuexiaojingfei from './component/Xuexiaojingfei'
import Xuexiaojingfeizhehe from './component/Xuexiaojingfeizhehe'
import Shuangdaitou from './component/Shuangdaitou'
import Renyuanpeixun from './component/Renyuanpeixun'
import Jieshouliuxuesheng from './component/Jieshouliuxuesheng'
import Kaifajiaoxuebiaozhun from './component/Kaifajiaoxuebiaozhun'
import Jianzhumianji from './component/Jianzhumianji'
import Tushuzongliang from './component/Tushuzongliang'
import Biyesheji from './component/Biyesheji'
import Biyeshengzongshu from './component/Biyeshengzongshu'
import Shehuipeixun from './component/Shehuipeixun'
import Laifangjiaoliu from './component/Laifangjiaoliu'
import Boshixuewei from './component/Boshixuewei'
import Fudaoyuanshuliang from './component/Fudaoyuanshuliang'
import Shixunjidi from './component/Shixunjidi'
import Dingdanzhanbi from './component/Dingdanzhanbi'
import Jiaoshishijian from './component/Jiaoshishijian'
import Hezuoqiyeshuliang from './component/Hezuoqiyeshuliang'
import Hezuoqiyetouru from './component/Hezuoqiyetouru'
import Zaixiaojianshe from './component/Zaixiaojianshe'
import Jishuchengguoshu from './component/Jishuchengguoshu'
import Xueshengchuangxin from './component/Xueshengchuangxin'
import Yingyongjishu from './component/Yingyongjishu'
import Zhuanyeshuzhanbi from './component/Zhuanyeshuzhanbi'
import Guojializhi from './component/Guojializhi'
import Shetuanrongyu from './component/Shetuanrongyu'
import Shetuanshuliang from './component/Shetuanshuliang'
import Xueshengdangyuan from './component/Xueshengdangyuan'
import Baodaolv from './component/Baodaolv'
import Luqufenshuxian from './component/Luqufenshuxian'
import Waishengshengyuan from './component/Waishengshengyuan'
import Zhaoshengshuliang from './component/Zhaoshengshuliang'
import Baimingxuesheng from './component/Baimingxuesheng'
import Jiaoxuekeyan from './component/Jiaoxuekeyan'
import Shengjunjiaoxue from './component/Shengjunjiaoxue'
import Shengjuntushu from './component/Shengjuntushu'
import Zichan from './component/Zichan'
import CanJiaGongYiHuoDong from './component/CanJiaGongYiHuoDong'
import HeZuoQiYeShuLiangKuaiJi from './component/HeZuoQiYeShuLiangKuaiJi'
import XueShenChuangXinChuangYe from './component/XueShenChuangXinChuangYe'
import XueShenWenYiTiYu from './component/XueShenWenYiTiYu'
import ZhuanYeShiJianXueShi from './component/ZhuanYeShiJianXueShi'

export default class Xianghao extends Component {
  render() {
    let autoplaySpeed = 40000;
      const settings = {
          // autoplay: true,
          autoplaySpeed,
          dots: true,
          infinite: false,
          slidesToShow: 1,
          slidesToScroll: 1,
          vertical: true,
          verticalSwiping: true,
          beforeChange: function (currentSlide, nextSlide) {
              // console.log("before change", currentSlide, nextSlide);
          },
          afterChange: (currentSlide) => {
              if (currentSlide === 4) {
                  setTimeout(() => {
                      if (this.slider && this.slider.goTo) {
                          this.slider.goTo(0)
                      }
                  }, autoplaySpeed)
              }
          },
      };
    return (
      <Carousel  ref={slider => (this.slider = slider)} {...settings} >
        <div>
          <div style={{ height:'calc(100vh - 74px)',overflowY:'auto'}}>          
            <Shengjunjiangzhu></Shengjunjiangzhu>
            <Xuexiaojingfei></Xuexiaojingfei>
            <Xuexiaojingfeizhehe></Xuexiaojingfeizhehe>
            <Shuangdaitou></Shuangdaitou>
            <Renyuanpeixun></Renyuanpeixun>
            <Jieshouliuxuesheng></Jieshouliuxuesheng>
            <Kaifajiaoxuebiaozhun></Kaifajiaoxuebiaozhun>
            <Jianzhumianji></Jianzhumianji>
            <Tushuzongliang></Tushuzongliang>
            <Biyesheji></Biyesheji>
            <Biyeshengzongshu></Biyeshengzongshu>
            <Shehuipeixun></Shehuipeixun>
            <Laifangjiaoliu></Laifangjiaoliu>
            <Boshixuewei></Boshixuewei>
            <Fudaoyuanshuliang></Fudaoyuanshuliang>
            <Shixunjidi></Shixunjidi>
            <Dingdanzhanbi></Dingdanzhanbi>
            <Jiaoshishijian></Jiaoshishijian>
            <Hezuoqiyeshuliang></Hezuoqiyeshuliang>
            <Hezuoqiyetouru></Hezuoqiyetouru>
            <Zaixiaojianshe></Zaixiaojianshe>
            <Jishuchengguoshu></Jishuchengguoshu>
            <Xueshengchuangxin></Xueshengchuangxin>
            <Yingyongjishu></Yingyongjishu>
            <Zhuanyeshuzhanbi></Zhuanyeshuzhanbi>
            <Guojializhi></Guojializhi>
            <Shetuanrongyu></Shetuanrongyu>
            <Shetuanshuliang></Shetuanshuliang>
            <Xueshengdangyuan></Xueshengdangyuan>
            <Baodaolv></Baodaolv>
            <Luqufenshuxian></Luqufenshuxian>
            <Waishengshengyuan></Waishengshengyuan>
            <Zhaoshengshuliang></Zhaoshengshuliang>
            <Baimingxuesheng></Baimingxuesheng>
            <Jiaoxuekeyan></Jiaoxuekeyan>
            <Shengjunjiaoxue></Shengjunjiaoxue>
            <Shengjuntushu></Shengjuntushu>
            <Zichan></Zichan>
          </div>
        </div>
        
        {/* 每一页的划分(学生维度-向好) */}
        <div>
          <div style={{ height:'calc(100vh - 74px)',overflowY:'auto'}}>
            <CanJiaGongYiHuoDong></CanJiaGongYiHuoDong>
          </div>
        </div>

        {/* 每一页的划分(专业维度-向好) */}
        <div>
          <div style={{ height:'calc(100vh - 74px)',overflowY:'auto'}}>
            <HeZuoQiYeShuLiangKuaiJi></HeZuoQiYeShuLiangKuaiJi>
            <XueShenChuangXinChuangYe></XueShenChuangXinChuangYe>
            <XueShenWenYiTiYu></XueShenWenYiTiYu>
            <ZhuanYeShiJianXueShi></ZhuanYeShiJianXueShi>
          </div>
        </div>
        
      </Carousel>
    )
  }
}
