import React, { Component } from 'react'
import Barchart from './barchart'
import JsonData from '../data/学校视角-基本情况-建筑面积.json'
export default class Shengjunjiangzhu extends Component {
    render() {
        return (
            <div className='item-wrapper'>
                <div className='item-box'>
                    <div className='item-title'>
                        <span>
                        建筑面积
                                    </span>

                    </div>
                    <div className='item-chart'>
                    <Barchart data={JsonData}></Barchart>
                    </div>
                </div>
            </div>
        )
    }
}